import React from "react"
import { Link } from 'react-router-dom'
import memberImageJSON from '../assets/memberImg.json'
import stateJSON from '../assets/state.json'

function Member(props) {
		let img = []
		let ctx = memberImageJSON[props._id]
		if(ctx){
			img.push(<img alt="" key={props._id + "img"} style={{height:"100px"}} src={memberImageJSON[props._id][Math.floor(Math.random()* memberImageJSON[props._id].length)]} />)
		}
		
		let party = props["congresses/partyAffiliations/name"]
		let partyColor = "green"
		if(party === "Democrat"){
			partyColor = "blue"
		}else if(party === "Republican"){
			partyColor = "red"
		}
	
    return (
		<tr>
			<td>
			<Link style={{textAlign:"center"}} to={{pathname:"/projects/react/hor/member/" + props._id, page:props.page, passChildData:props.getChildData }}>{props.officialName}<br />{img}</Link>
			</td>
			<td><span style={{color:partyColor}}>{party}</span></td>
			<td>{stateJSON[props["congresses/stateCode"]]}</td>
			<td>{props["congresses/stateDistrict"]}</td>
			<td>{props.status}</td>
			<td>{props.active}</td>
		</tr>
    )
}

export default Member